.container{
  width: 100%;
}
@media (min-width: 640px){


  .container{
    max-width: 640px;
  }
}
@media (min-width: 768px){


  .container{
    max-width: 768px;
  }
}
@media (min-width: 1024px){


  .container{
    max-width: 1024px;
  }
}
@media (min-width: 1280px){


  .container{
    max-width: 1280px;
  }
}
@media (min-width: 1536px){


  .container{
    max-width: 1536px;
  }
}
.visible{
  visibility: visible;
}
.static{
  position: static;
}
.fixed{
  position: fixed;
}
.absolute{
  position: absolute;
}
.relative{
  position: relative;
}
.\!relative{
  position: relative !important;
}
.sticky{
  position: sticky;
}
.block{
  display: block;
}
.inline{
  display: inline;
}
.flex{
  display: flex;
}
.table{
  display: table;
}
.hidden{
  display: none;
}
.h-screen{
  height: 100vh;
}
.w-screen{
  width: 100vw;
}
